import React from "react"
import { Link, graphql } from "gatsby"
import Img from "gatsby-image"
import styled from "styled-components"

import Layout from "../components/layout"
import SEO from "../components/seo"
import logo from "../images/Team-Sodo-Logo-White.svg"
import Header from "../components/header"
import Title from "../components/project-title"

const StyledLink = styled(Link)`
  position: relative;
  text-decoration: none;
  z-index: 20;
  left: 5rem;
  top: 5vh;
`;

const Logo = styled.img`
  max-width: 6rem;
  margin: 0;
`;

const Image = styled(Img)`
  bottom: 8vh;
  left: 1.25rem;
  right: 1.25rem;
  top: 1.25rem;
`;

const Container = styled.div`
  position: relative;
  z-index: 20;
  padding-top: 1.25rem;
  background: #ffffff;
  margin-top: calc(70vh - 1.25rem);
`;


const Content = styled.div`
  max-width: 1080px;
  margin: 1rem auto;
  padding: 0 1.25rem;
`;

class IndexPage extends React.Component {
  constructor() {
    super(); 
  }

  render() {
    return (
    <Layout>
      <SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
      {/* <StyledLink to="/">
          <Logo src={logo}></Logo>
      </StyledLink> */}
      <Header siteTitle={"Team Sodo"} />
      <Image 
      fluid={this.props.data.hero.childImageSharp.fluid} 
      style = {{ position: `fixed`}}
      />
      <Container>
        <Content>
          <Title/>
          <h1>This is proxima nova</h1>
          <p>apparently we need a landing page </p>
          <div style={{ maxWidth: `300px`, marginBottom: `1.45rem` }}>
          </div>
          {/* <Link to="/page-2/">Go to page 2</Link> */}
        </Content>
      </Container>
  </Layout>
    )
  }
}

export default IndexPage;

export const query = graphql`
  {
    hero:file(relativePath: {eq: "images/hero.jpg"}) {
      childImageSharp {
        fluid (maxWidth: 2400) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
                                                                              